@import url('https://fonts.googleapis.com/css?family=Roboto');

body{
    font-size: 16px;
}



#nav-menu{
    border: 0.5px solid gray;
    background: radial-gradient(circle at 10% 20%, rgb(255, 229, 168) 0%, rgb(251, 174, 222) 100.7%);
    display: flex;
    justify-content: space-between;
    width: 80%;
    height: 80px;
    border-radius: 50px;
    position: fixed;
    left:10%;
    top:2%;
    margin-bottom: 100px;
    
}




.logo {
  
    border-radius: 50%;
    width: 80px;
    min-width: 10px;
    max-width: 10%;
    height:80px;
    max-height: auto;
    margin-left: 0px;

}

.logo img{

    height: 75px;
    width: 75px;
    border-radius: 50%;
    /* border: 2px solid black; */
    border: 2px solid #4a00e0;

}


.logo a {

    text-decoration: none;
}


 .logo a:hover {
    
   
}



.main-list{
    margin-right: 1%;
    width: 70%;
    /* padding: 10px; */


}
.main-list  button{
     border-radius: 50%;
     height: auto;
     

     
}



#nav-menu ul>li{
     list-style: none;
     

}

#nav-menu ul>li>a{
     text-decoration: none;
     color: #000;
    
}

#nav-menu>div{
     
     /* border: 1px solid black; */
     margin-right: 32px;
}


.ul-links{
    display: flex;
    justify-content: space-between;
      align-items: center; 
      font-size: 16px;
      margin-top: 25px;
      flex-wrap: wrap;
      margin-right: 20px;
      /* border: 1px solid black; */
      
 
    
}

#dark-mode-btn{
     font-size: 20px;

}
.dark-mode-btn{
    margin-top: 24px;
    border-radius: 50%;
}
.dark-mode-btn>button{
    border-radius: 50%;
}

#burger-menu{
    display: none;

}

#ho,#ab,#sk,#co,#pr,#resume-link-1,#st{
    display: flex;
    align-items: center;
    position: relative;
    
}

#ho::after,#ab::after,#sk::after,#co::after,#pr::after,#resume-link-1::after,#st::after{
    content: "";
    position: absolute;
    left: 0;
    bottom: -2px; /* Adjust this value to control the distance between text and underline */
    width: 100%;
    height: 2px;
    background-color: rgb(83, 5, 5); /* Set the color of the underline */
    transform: scaleX(0); /* Start with no underline */
    transform-origin: left;
    transition: transform 0.3s ease; 
}

#ho:hover::after,#ab:hover::after,#sk:hover::after,#co:hover::after,#pr:hover:after,#resume-link-1:hover:after,#st:hover:after{

    transform: scaleX(1);
}
#h1-name{
 display: none;       
}



@media  (min-width:300px) and (max-width:650px){
    #burger-menu{
        display: block;

     }
     .ul-links{
        display: block;
        padding: 0;
        margin: 0;
     }
     .ul-links li{
         padding: 10px;
         margin-right: 10px;
     }
   
     .main-list{
        
        display: none;
        position: absolute;
        top: 100px;
        left: 65vw;
        border: 1px solid black;
        border-radius: 12px;
         padding: 10px;
         padding-right: 20px;
        /* background-color: pink; */
        background-color: rgba(255, 192, 203, 0.5); 
        overflow-x: hidden;

     }

       .dark-mode-btn{
        display: none;
     }


   #nav-menu{
      width: 100%;
